

































import { Vue, Prop, Component } from 'vue-property-decorator';

@Component
export default class Article extends Vue {
  @Prop(String) public id!: string;
  public model: object = {};
  public created() {
    this.fetch();
  }
  public async fetch() {
    const res = await this.$axios.get(`article/${this.id}`);
    this.model = res.data;
  }
}
